.main-footer {
  @include flex-column;
  background-color: $footer-grey;
}

.main-footer__upper-part-wrapper {
  @include desktop-container;
  @include flex-row;
  margin-top: 65px;
  padding-right: 8px;
}

.main-footer__logo-social-wrapper {
  @include empty-rule;
}

.main-footer__logo-wrapper {
  margin-bottom: 55px;
}

.main-footer__logo {
  @include empty-rule;
}

.main-footer__logo p {
  color: $logo-black;
}

.main-footer__social {
  margin-bottom: 59px;
}

.main-footer__info-wrapper {
  @include flex-row;
  margin-left: auto;
}

.main-footer__sections-wrapper {
  margin-right: 356px;
  margin-left: 0;
}

.main-footer__sections-wrapper h3 {
  margin-left: 9px;
}

.main-footer__contact-details-wrapper {
  margin-left: auto;
  margin-right: 0;
}

.main-footer__contact-details-wrapper h3 {
  margin-left: 2px;
}

.main-footer__info-wrapper h3 {
  @include reset-space;
  @include roboto-condensed-font;
  color: $dark-grey;
  text-transform: uppercase;

  font-size: 15px;
  line-height: 16px;
  font-weight: 400;
  letter-spacing: 0.05em;

  margin-bottom: 25px;
}

.main-footer__btn {
  display: none;
}

.main-footer__list {
  @include clear-list;
  @include flex-column;

  &--sections {
    flex-wrap: wrap;

    max-height: 100px;
  }

  &--contact-details {
    @include empty-rule;
  }
}

.main-footer__sections-item {
  margin-right: 66px;
  padding-left: 8px;
}

.main-footer__sections-link {
  @include active-states;
  font-size: 15px;
  line-height: 25px;
  color: $dark-grey-transparent;
}

.main-footer__contact-details-item {
  font-size: 15px;
  line-height: 25px;
  color: $dark-grey-transparent;
}

.main-footer__contact-details-link {
  @include active-states;
  text-decoration: none;
  color: $dark-grey-transparent;
}

.main-footer__lower-part-wrapper {
  @include border-top;
}

.main-footer__info-list {
  @include clear-list;
  @include desktop-container;
  @include flex-row;
  justify-content: space-between;
  padding-right: 8px;
}

.main-footer__info-item {
  margin: 14px auto;
}

.main-footer__info-item[data-footer-number="1"] {
  margin-left: 0;
  margin-right: 76px;
}

.main-footer__info-item[data-footer-number="2"] {
  margin-left: 0;
  margin-right: 141px;
}

.main-footer__info-item[data-footer-number="3"] {
  margin-left: 0;
  margin-right: auto;
}

.main-footer__info-item[data-footer-number="4"] {
  margin-left: auto;
  margin-right: 0;
}

.main-footer__info-item p,
.main-footer__info-item a {
  @include reset-space;
  font-size: 13px;
  line-height: 24px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $dark-grey-transparent;
}

.main-footer__info-item a {
  @include reset-space;

  &:hover,
  &:focus {
    color: $logo-black;
  }
}

@media (max-width: $tablet-width-only) {
  .main-footer {
    @include empty-rule;
    position: relative;
  }

  .main-footer__upper-part-wrapper {
    @include reset-space;
    flex-wrap: wrap;
    width: auto;
  }

  .main-footer__logo-social-wrapper {
    @include border-bottom;
    @include flex-row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-top: 19px;

    padding-top: 0;
    padding-bottom: 25px;
    padding-left: 25px;
    padding-right: 0;
  }

  .main-footer__info-wrapper {
    justify-content: space-between;
    margin-top: 38px;
    margin-bottom: 36px;
    padding: 0 25px;
    width: 100%;
  }

  .main-footer__sections-wrapper {
    margin-left: 0;
    margin-right: auto;
  }

  .main-footer__contact-details-wrapper {
    margin-right: -4px;
  }

  .main-footer__sections-wrapper h3,
  .main-footer__contact-details-wrapper h3 {
    margin-left: 2px;
  }

  .main-footer__lower-part-wrapper {
    @include empty-rule;
  }

  .main-footer__logo-wrapper {
    margin: 0;
  }

  .main-footer__logo {
    @include empty-rule;
  }

  .main-footer__social {
    margin: 0;
  }

  .main-footer__list {
    &--sections {
      flex-wrap: no-wrap;
      max-height: 90vh;
    }

    &--contact-details {
      @include empty-rule;
    }
  }

  .main-footer__sections-item {
    padding-left: 0;
  }

  .main-footer__sections-link {
    @include empty-rule;
  }

  .main-footer__contact-details-item {
    @include empty-rule;
  }

  .main-footer__info-list {
    width: auto;
    padding: 0 25px;
    padding-right: 10px;
  }

  .main-footer__info-item {
    margin-top: 21px;
    margin-bottom: 24px;
  }

  .main-footer__info-item[data-footer-number="1"] {
    display: none;
  }

  .main-footer__info-item[data-footer-number="2"] {
    position: absolute;
    top: 16px;
    left: 328px;
  }

  .main-footer__info-item a {
    &:hover,
    &:focus {
      color: $logo-black;
    }
  }
}

@media (max-width: $mobile-width-only) {
  .main-footer {
    @include empty-rule;
  }

  .main-footer__upper-part-wrapper {
    @include empty-rule;
  }

  .main-footer__logo-social-wrapper {
    @include flex-column;
    justify-content: space-between;
    margin-top: 46px;
    padding: 0;
    padding-bottom: 47px;
  }

  .main-footer__info-wrapper {
    @include flex-column;
    padding: 0;
    margin-top: 29px;
    margin-bottom: 26px;
  }

  .main-footer__sections-wrapper {
    @include border-bottom;
    @include flex-row;
    flex-wrap: wrap;
    width: 100%;
    padding: 0 15px;
    margin: 0;
  }

  .main-footer__contact-details-wrapper {
    @include flex-row;
    flex-wrap: wrap;
    width: 100%;
    padding: 0 15px;
    margin: 0;
    margin-top: 28px;
  }

  .main-footer__sections-wrapper h3,
  .main-footer__contact-details-wrapper h3 {
    margin-left: 0;
  }

  .main-footer__lower-part-wrapper {
    @include empty-rule;
  }

  .main-footer__logo-wrapper {
    margin-bottom: 80px;
  }

  .main-footer__logo {
    @include empty-rule;
  }

  .main-footer__social {
    margin: 0;
  }

  .main-footer__info-wrapper h3 {
    margin-bottom: 27px;
  }

  .main-footer__btn {
    display: block;
    position: relative;
    align-self: start;

    width: 16px;
    height: 16px;

    margin-left: auto;

    border: none;
    background-color: transparent;

    &::before {
      left: 0;
    }

    &::after {
      transform: rotate(90deg);
      left: 0;
    }

    &::after,
    &::before {
      content: "";
      position: absolute;
      width: 16px;
      height: 1px;
      background-color: transparent;
    }
  }

  .main-footer__btn--hide {
    &::before {
      background-color: $social-grey;
    }

    &::after {
      background-color: transparent;
    }
  }

  .main-footer__btn--show {
    &::before {
      background-color: $social-grey;
    }

    &::after {
      background-color: $social-grey;
    }
  }

  .main-footer__btn--nojs {
    &::before {
      background-color: transparent;
    }

    &::after {
      background-color: transparent;
    }
  }

  .main-footer__list {
    &--sections {
      margin-top: 5px;
      margin-bottom: 23px;
    }

    &--contact-details {
      margin-top: 5px;
    }

    &--hidden {
      display: none;
    }
  }

  .main-footer__sections-item {
    @include empty-rule;
  }

  .main-footer__sections-link {
    @include empty-rule;
  }

  .main-footer__contact-details-item {
    max-width: 80%;
  }

  .main-footer__info-list {
    @include flex-column;
    justify-content: space-between;
    align-items: center;
    margin-top: 28px;
    margin-bottom: 5px;
  }

  .main-footer__info-item {
    @include empty-rule;
  }

  .main-footer__info-item[data-footer-number="1"] {
    display: none;
  }

  .main-footer__info-item[data-footer-number="2"] {
    position: absolute;
    top: 103px;
    left: 45px;

    min-width: 233px;
  }

  .main-footer__info-item[data-footer-number="3"],
  .main-footer__info-item[data-footer-number="4"] {
    margin: 0;
    margin-bottom: 21px;
    margin-left: -14px;
  }
}
